import React, { } from 'react';
import { Link } from 'react-router-dom';
import {get, sync_master_data} from '../../helpers/storage_helper';
import {get_country_master_data} from '../../actions/common_actions';

import Modal from 'react-bootstrap/Modal';

export class SelectMarket extends React.Component {
  handleClose = (event) => {
    this.setState({ show: false });
  }



  clickMarket = (event) => {
    //alert(event.target.id);
    
    const market = this.market_list[event.target.id];
    
     const country_code = market.country_code;
     const time_zone = market.time_zone;
     const master_data_version = get('master_data_version');
    get_country_master_data({country_code, time_zone})
       .then((response) => {
          if(!response){return };
            if(response.status === "success"){
                sync_master_data(response.data.master_data, response.data.master_data_version);            
            }else{
              alert(response.message + "\n" + JSON.stringify(response.data));  
            }
          
        });     

     localStorage.setItem('market', JSON.stringify(market));
    
     this.props.onSelect();
   }
 componentWillMount(){
    //alert("SelectMarket");
    this.market_list = get('market_list');
    this.setState({ show: true });
    
    const marketLinks = this.market_list.map((market, index)=>
        <ul  key={index}>
          <Link to="#">
            <li id={index} variant="primary" onClick={this.clickMarket.bind(this)}>
            {market.country_code}
            </li> 
          </Link>
        </ul>
      );
    this.setState({marketLinks: marketLinks});
 }

  render() {
 
    return (
      <>
        <Modal show={this.state.show} onHide={this.handleClose}>
          <Modal.Header>
            <Modal.Title>Hello Super Admin!</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <h4>Please select the market you would like to administrate.</h4>
           {this.state.marketLinks}
          </Modal.Body>
        </Modal>
      </>
    );
  }
}

export default SelectMarket;