
import './styles/flow.css';
import React, { Component } from 'react';
//import { BrowserRouter as Router,  Switch} from 'react-router-dom';
import { Router,  Switch} from 'react-router-dom';
import Route from 'react-router-dom/Route';
import {get} from './helpers/storage_helper';
import Login from './UI/auth/component/Login';
import Logout from './UI/auth/component/Logout';
import AdminHome from './UI/home/AdminHome';
import SelectMarket from './UI/home/SelectMarket';
import history from "./history";
import $ from 'jquery';
import 'react-datetime/css/react-datetime.css';
$.dataTable = require('datatables.net');

require("datatables.net-buttons/js/dataTables.buttons.js");
require("datatables.net-buttons/js/buttons.colVis.js");
require("datatables.net-buttons/js/buttons.flash.js");
require("datatables.net-buttons/js/buttons.html5.js");
require("datatables.net-buttons/js/buttons.print.js");



class App extends Component {

  constructor(){
    super();
    this.loadUser = this.loadUser.bind(this)
    console.log(App.state)
  }

  state = {
  
    routes: [],
    home: [],
    selectMarket: false
  }
  componentWillMount(){
    this.loadUser();

  }

   loadScreen = (screen) => {
    
      if(screen === "admin"){
       
        this.setState({home: <AdminHome/>});

        
      // this.setState({routes: AdminRoutes});
      }/*else if(app == "public"){
        this.setState({home:  <PublicHome/>});
      }*/
      else if(screen === "login"){
       
        this.setState({home: <Login onLogon={this.loadUser}/>});
      }
      else if(screen === "selectMarket"){
       
        this.setState({home: <SelectMarket onSelect={this.loadUser}/>});
      }
  }
  


  loadUser() {
    
      let role_codes = get('role_codes');
    
      let screen = null;
      
      let market = get('market');
      
      if(role_codes && role_codes.includes('super_admin')){
        // let market_list = get('market_list');
         if(!market){   
            screen = "selectMarket";
            //this.setState({selectMarket: true})
          }else  if(market){     
            screen = "admin"; // Show screen for Super Admin for the 'market' 
          }else{
            alert("Unable to load Market for Super Admin")
          }
      }else if(!role_codes){
        
        screen = "login"; // Show login screen
        
      }else if(market){ 
         screen = "admin"; // // Show screen for non Super Admin for the 'market'        

      }else{
          alert("Unable to obtain User's Market Information or No Market has been configured in the system")
      }
       
       this.loadScreen(screen);
}
  render() {
    
    return (
        <div  className="App">
              <Router history={history}>
              <Switch>   
              <Route  path={"/login"} component={() => <Login onLogon={this.loadUser}/>}/>
              <Route  path={"/logout"} component={() => <Logout/>}/>
                <Route path={"/"}>
                  <>
                    {this.state.home}
                  {/*{this.state.routes}
                  <Redirect from="*" to="/login"/>*/}
                  </>
                </Route>

              </Switch>      

            </Router>     
        </div>
        
    );
  }
}

export default App;
